<template>

    <div class="top-title">
      <h1>Express Yourself with Emoticons</h1>
      <h4>Enhance your social media posts, messages,</h4>
      <h4>and more with our vast collection of expressive emoticons.</h4>
    </div>

    <!-- Emoji Selector -->
    <div class="box-r">
      <div class="emoji-selector">
        <!-- Emotion Buttons -->
        <div class="category-group">
          <div class="buttons">
            <button
              :class="{ active: selectedCategory === 'All' }"
              @click="selectCategory('All')"
              class="category-button"
            >
              All
            </button>
            <button
              v-for="emotion in emotions"
              :key="emotion"
              :class="{ active: selectedCategory === emotion }"
              @click="selectCategory(emotion)"
              class="category-button"
            >
              <span class="emoji-icon">{{ getEmojiForCategory(emotion)[0] }}</span>
              {{ emotion }}
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Emoji List -->
    <div class="emoji-list">
      <div v-if="selectedCategory === 'All'">
        <h2>All Emojis:</h2>
        <div class="all-emoji-list">
          <div v-for="(emojiList, category) in emojisData" :key="category" class="category-section">
            <h3>{{ category }}</h3>
            <div class="emoji-items">
              <button
                v-for="(emoji, index) in emojiList"
                :key="index"
                class="emoji-item"
                :class="{ enlarged: selectedEmoji === emoji }"
                @click="setSelectedEmoji(emoji)"
              >
                {{ emoji }}
              </button>
            </div>
          </div>
        </div>
      </div>
      <div v-else>
        <h2 v-if="currentEmojis.length > 0">Emojis for {{ selectedCategory }}:</h2>
        <div v-if="currentEmojis.length > 0" class="emoji-items">
          <button
            v-for="(emoji, index) in currentEmojis"
            :key="index"
            class="emoji-item"
            :class="{ enlarged: selectedEmoji === emoji }"
            @click="setSelectedEmoji(emoji)"
          >
            {{ emoji }}
          </button>
        </div>
        <p v-else>No emojis available for this category.</p>
      </div>
    </div>

    <!-- Footer for copying emoji -->
    <div class="f-r">
      <div class="f-text">Click on an emoji.</div>
      <div class="f-container">
        <input v-model="selectedEmoji" type="text"  class="f-input"/>
        <button @click="copyToClipboard" class="f-button">Copy</button>
      </div>
    </div>

</template>
<script>
import emojisData from '../assets/js/emojis.js'

export default {
  data () {
    return {
      selectedCategory: 'All',
      emotions: Object.keys(emojisData), // List of categories
      emojisData, // Make emojisData available in the template
      selectedEmoji: '' // Track the currently selected emoji
    }
  },

  computed: {
    currentEmojis () {
      if (this.selectedCategory === 'All') {
        // Return all emojis combined
        return Object.values(this.emojisData).flat()
      } else {
        // Return emojis for the selected category
        return this.getEmojiForCategory(this.selectedCategory)
      }
    }
  },

  methods: {
    selectCategory (category) {
      this.selectedCategory = category
    },

    getEmojiForCategory (category) {
      return this.emojisData[category] || []
    },

    setSelectedEmoji (emoji) {
      this.selectedEmoji += emoji
      navigator.clipboard.writeText(this.selectedEmoji)
    },

    copyToClipboard () {
      navigator.clipboard.writeText(this.selectedEmoji)
        .then(() => {
          document.querySelector('.f-text').textContent = 'Copied to clipboard!'
        })
        .catch(err => {
          console.error('Could not copy text: ', err)
        })
    }
  }
}
</script>
